header {
   .top {
      // background: honeydew
      height: 150px;
      display: flex;

      span {
         // background: pink
         display: flex;
         // align-items: center

         h2 {
            color: #67809f;
            // border: #67809f solid 5px

            margin-top: 5px;
            padding: 5px 15px;

            font-family: $font-8;
            font: {
               size: 50px;
               weight: 700; }
            letter-spacing: 0.05em; } }

      nav {
         // background: #89c4f4
         // floats don't work in flexbox.
         margin-left: auto;
         margin-right: 10px;
         margin-top: 25px;

         a {
            // background: #5c97bf
            color: #2e3131;

            margin: 0 10px;

            text-decoration: none;
            text-transform: uppercase;
            font-family: $font-8;
